


import { TextField } from '@mui/material'
import { Button } from '@mui/material';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import { CSSTransition } from 'react-transition-group';

import { setLogInStatus, reduxualBarOpen, reduxualBarClose } from '../redux/actions';
import { Link, withRouter } from 'react-router-dom/cjs/react-router-dom.min';

function mapStateToProps(state) {


    return {

    };
}

const dispatch = {
    setLogInStatus: setLogInStatus,
    reduxualBarOpen: reduxualBarOpen,
    reduxualBarClose: reduxualBarClose
}

class SignUp extends Component {

    state = {

        // data:
        mobile: "",
        firstname: "",
        lastname: "",
        password: "",

        // variables:
        showAlert: false,
        showFailAlert: false,
        alertMessage: "",
        alertMessage2: "",

    }




    showSuccessAlert = (message, subMessage) => {
        this.props.reduxualBarOpen(message, subMessage)

        setTimeout(this.props.reduxualBarClose, 3000)
    }


    componentDidMount() {
        try {

            this.props.goTop()
        } catch {

        }

    }



    showFailAlert = (message, subMessage) => {
        this.setState({
            showFailAlert: true,
            alertMessage: message,
            alertMessage2: subMessage
        })

        setTimeout(() => this.setState({ showFailAlert: false, alertMessage: "", alertMessage2: "" }), 5000)
    }





    handleCreateUser = e => {
        e.preventDefault()

        fetch("https://pardehmahoor.ir/digitalAssets/registerUser", {
            method: "POST",
            body: JSON.stringify({

                mobile: this.state.mobile,
                password: this.state.password,
                firstname: this.state.firstname,
                lastname: this.state.lastname

            })
        })
            .then(res => res.json())
            .then(data => {
                console.log(data)
                if (data.status === 200) {

                    this.props.history.push("/Login")
                    this.showSuccessAlert("   ", "    ")

                    return
                }
                if (data.status === "DUP") {
                    this.showFailAlert("      ", "    ")
                }


            })


    }


    render() {
        return (
            <div className="col-12 center mt-5">

                <form type="submit" className='col-12 center flex-column  border position-relative paymentStatsCont p-4 align-items-start justify-content-start flex-column' onSubmit={this.handleCreateUser}>

                    <div className="col-12 LoginHead">
                        <h1 className=""> </h1>
                    </div>
                    <div className="m-3"></div>

                    <div className="col-12 center flex-column">

                        <TextField InputLabelProps={{ required: false }} variant="standard" required onChange={event => {
                            this.setState({ mobile: event.target.value })

                        }} id="standard-basic" className=' dirRtl textAreaForLogIn m-2' label="" />

                        <TextField InputLabelProps={{ required: false }} variant="standard" required onChange={event => {
                            this.setState({ firstname: event.target.value })

                        }} id="standard-basic" className=' dirRtl textAreaForLogIn m-2' label="" />

                        <TextField InputLabelProps={{ required: false }} variant="standard" required onChange={event => {
                            this.setState({ lastname: event.target.value })

                        }} id="" className=' dirRtl textAreaForLogIn m-2' label=" " />


                        <TextField type='password' InputLabelProps={{ required: false }} variant="standard" required onChange={event => {
                            this.setState({ password: event.target.value })

                        }} id="" className=' dirRtl textAreaForLogIn m-2' label=" " />

                        <div className="col-12 mt-5 mb-5 center flex-column">
                            <Button variant="contained" type="submit" className="whiteT loginBtn">  </Button>
                            <Button variant="outlined" className="whiteT loginBtn signUpBtn"><Link className="col-12 h-100 " to="/Login">  </Link></Button>
                        </div>

                    </div>

                </form>

                <CSSTransition
                    in={this.state.showAlert}
                    classNames="opac"
                    timeout={300}
                    unmountOnExit
                >

                    <div className="col-12 alertCont">
                        <Alert severity="success" variant='filled'>
                            <AlertTitle></AlertTitle>
                            {this.state.alertMessage}  <strong> {this.state.alertMessage2}</strong>
                        </Alert>
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.state.showFailAlert}
                    classNames="opac"
                    timeout={300}
                    unmountOnExit
                >

                    <div className="col-12 alertCont">
                        <Alert severity="error" variant='filled'>
                            <AlertTitle></AlertTitle>
                            {this.state.alertMessage}  <strong> {this.state.alertMessage2}</strong>
                        </Alert>
                    </div>
                </CSSTransition>


            </div>

        );
    }
}

export default withRouter(connect(
    mapStateToProps, dispatch
)(SignUp));