
import React, { Component } from 'react'
import { connect } from 'react-redux'
import NeshanMap from "@neshan-maps-platform/react-openlayers"
import { createRef } from 'react'
import { Fragment } from 'react'
import { TextField } from '@mui/material'
import { setSearchPlaceItems, clearSearchPlaceItems } from '../redux/actions'
import { CSSTransition } from 'react-transition-group'
import AddressItem from './AddressItem'
import { Button } from '@mui/material'



export class NeshanMapC extends Component {

    state = {
        center: { latitude: 36.25421084784807, longitude: 59.62030814049516 }
    }
    mapRef = new createRef()

    lat = 0
    longt = 0
    handleMapChange = () => {
        console.log(this.mapRef.getView().getCenter())
    }

    searchPlaces = e => {
        if (e.target.value === "") {
            this.props.clearSearchPlaceItems()
            return
        }

        console.log("prop !!!!!!", this.props.searchPlaceOpen)


        fetch("http://192.168.1.192:8880/digitalAssets/searchPlace", {
            method: "POST",
            body: JSON.stringify({ center: this.state.center, querry: e.target.value })
        })
            .then(res => res.json())
            .then(data => {
                this.props.setSearchPlaceItems(data.locations.items)
                console.log(data)
            })

    }

    changeCanvas = () => {
        var coordinates = this.mapRef.current.map.getView().getCenter()
        var raw = this.mapRef.current.ol.proj.toLonLat(coordinates)

        fetch("http://192.168.1.192:8880/digitalAssets/getAddress", {
            method: "POST",
            body: JSON.stringify({ lat: raw[1], longt: raw[0] })
        })
            .then(res => res.json())
            .then(data => console.log(data))
    }



    changeCenter = (lat, logt) => {
        console.log(lat, logt)
        
        console.log(this.mapRef.current)
        console.log(this.mapRef.current.ol.View.prototype)
        const view = this.mapRef.current.ol.View.prototype
        // this.setState({ center: { latitude: logt, longitude: lat } })
        view.setCenter(this.mapRef.current.ol.proj.transform([logt, lat], 'EPSG:4326', 'EPSG:3857'))
        // view.setCenter( logt, lat )
        console.log(this.mapRef.current.map.getView().getCenter())

    }

    render() {
        return (
            <Fragment>
                <div className='position-relative'>
                    <div className=' searchPlacesInputCont center h-fit'>
                        <div className='searchPlacesInput center'>
                            <TextField onChange={this.searchPlaces} label="  " variant="standard" />
                            <CSSTransition in={this.props.searchPlaceOpen} classNames="opac" timeout={100} unmountOnExit>
                                <div className='locationCOnt '>
                                    <div className='locationItemCont position-relative'>
                                        <p className='border-bottom col-3 adTitleCont'>   </p>
                                        <div className='m-5'></div>
                                        {this.props.searchPlaces.length != 0 &&
                                            this.props.searchPlaces.map(i => <AddressItem changeCenter={this.changeCenter} location={i.location} title={`${i.address}, ${i.title}`}></AddressItem>)}
                                    </div>
                                </div>
                            </CSSTransition>

                        </div>
                    </div>

                    <CSSTransition
                        in={true}
                        classNames="opac"
                        timeout={100}
                        unmountOnExit
                    >
                        <Fragment>

                            <NeshanMap
                                ref={this.mapRef}
                                style={{ height: "-webkit-fill-available !important", width: "100% !important", backdropFilter: "blur(15px)", zIndex: "999" }}
                                mapKey="web.a60fbd5f77844b8d95a7283a8a5457fe"
                                target=""
                                center={this.state.center}
                                defaultType="standard-night"
                            ></NeshanMap>

                            <div className='NeshanBtnCont center col-12 h-fit'>
                                <Button className='NeshanBtn' variant="contained" onClick={this.changeCanvas}>  </Button>
                            </div>
                        </Fragment>
                    </CSSTransition>
                </div>
            </Fragment >
        )
    }
}

const mapStateToProps = (state) => {
    return {
        searchPlaceOpen: state.searchPlaceOpen,
        searchPlaces: state.searchPlaces
    }
}

const mapDispatchToProps = {
    clearSearchPlaceItems: clearSearchPlaceItems,
    setSearchPlaceItems: setSearchPlaceItems
}

export default connect(mapStateToProps, mapDispatchToProps)(NeshanMapC)
