

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { ReactComponent as Logo } from '../assets//MahoorLogoType.svg';
import { CSSTransition } from 'react-transition-group'
import { InView } from 'react-intersection-observer';
import { Fragment } from 'react';

export class SpecialProducts extends Component {

    state = {
        fourthImgIsInView: false,
        title: null,
        content: null
    }


    getData = () => {

        fetch("https://pardehmahoor.ir/webInfo/getAboutInfo")
            .then(r => r.json())
            .them(d => this.setState({ title: d.title, content: d.content }))
    }


    render() {
        return (
            <div className={`specialCont center ${this.props.navBarSmallView && "mt-5"}`}>
                <div >

                    <div className='col-12 col-xl-8  posrel d-flex flex-column-reverse flex-xl-row '>
                        <div className='col-12 col-xl-6 specialInfoCont center justify-content-between justify-content-xl-between pt-0'>
                            <div className='col-12 col-xl-12 center flex-column'>

                                <InView className={`col-9 col-xl-12 d-flex justfy-content-end flex-column ${this.props.navBarSmallView && "mt-3"}`} onChange={(inView, entry) => {
                                    this.setState({ fourthImgIsInView: inView })
                                    console.log(inView)
                                }}>

                                    <CSSTransition
                                        in={this.state.fourthImgIsInView}
                                        timeout={1000}
                                        classNames="right-to-left"
                                        unmountOnExit
                                    >





                                        <h1 className=''>   </h1>


                                    </CSSTransition>
                                    <CSSTransition
                                        in={this.state.fourthImgIsInView}
                                        timeout={1000}
                                        classNames="BTT"
                                        unmountOnExit
                                    >
                                        <p>                     .                                          </p>
                                    </CSSTransition>
                                </InView>
                            </div>

                        </div>
                        <div className='col-12 col-xl-6 center justify-content-center justify-content-xl-end align-items-start'>
                            <CSSTransition
                                in={this.state.fourthImgIsInView}
                                timeout={1000}
                                classNames="left-to-right"
                                unmountOnExit
                            >
                                <Logo className='mahoorLogoBig' />
                            </CSSTransition>
                        </div>

                    </div>
                </div>


            </div >
        )
    }
}

const mapStateToProps = (state) => {
    return {
        navBarSmallView: state.navBarSmallView
    }
}

const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(SpecialProducts)