


import React, { Component } from 'react'
import { connect } from 'react-redux'
import Product from './Product'
import { Link } from 'react-router-dom'
import { Button } from '@mui/material'
import SmallImageRef from './SmallImageRef'
import ExpandCircleDownIcon from '@mui/icons-material/ExpandCircleDown';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { Fragment } from 'react'
import { CSSTransition } from 'react-transition-group'
import { InView } from 'react-intersection-observer';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

export class Section extends Component {
    state = {
        more: false,
        subDivs: [],
        guiderOpen: false
    }


    componentDidMount() {
        fetch("https://pardehmahoor.ir/digitalAssets/getDivRel", {
            method: "POST",
            body: JSON.stringify({ divisionName: `${this.props.divisionData.name}` })
        })
            .then(res => res.json())
            .then(data => {
                console.log(data)
                this.setState({ subDivs: data.data })
            })

    }

    render() {
        console.log("dd", this.props.divisionData)
        const Prods = this.props.productData.filter(i => i.divisions == this.props.divisionData.name)
        console.log(Prods)


        if (Prods.length !== 0) {

            return (
                <div className={` col-12 p-xl-4  center flex-column oHidd shad position-relative sectWidthFix punchIn ${this.props.navBarSmallView && "p-0"}`}>
                    <div className={` d-flex col-12  center categoryInformation justify-content-end ${this.props.navBarSmallView && "p-5 pb-2"}`}>
                        {
                            this.state.subDivs != [] &&
                            this.state.subDivs.map(i =>
                                <Link className='navButton m-1' exact to={`/category/${this.props.divisionData.name}/${i}/`} >/{i}</Link>
                            )}
                        <h2 className='bold grayText m-1 headingTitleForCategory'>{this.props.divisionData.name}</h2>
                    </div>
                    <div className={`col-12 center flex-row align-items-start justify-content-start sectionCont p-0 ${this.props.navBarSmallView && "flex-column-reverse"} ${this.props.navBarSmallView && "flex-column-reverse"}`}>

                        <div className={`d-flex flex-row glow-on-hover oHidd col-12 col-xl-9 position-relative  borderRadu52 ${!this.props.navBarSmallView && "rightSpacorForSection"}`}>

                            <Link className='prodsHeading mar-r center ' exact to={`/category/${this.props.divisionData.name}`}> <ChevronLeftIcon></ChevronLeftIcon> </Link>

                            <div className='overX d-flex flex-row zindexForProdCont'>
                                {

                                    Prods.slice(0, 4).map(i =>
                                        <div className='sectionProdCont '>
                                            <Product title={i.title} price={i.price} img={i.imgUrl} data={i}/>
                                        </div>
                                    )}
                            </div>

                            {!this.props.navBarSmallView &&
                                <div className='position-absolute bluryDiv '></div>
                            }


                        </div>


                        <InView className={` ${!this.props.navBarSmallView && "imageForDivision col-3 center flex-column justify-content-start "} ${this.props.navBarSmallView && "col-12"}`} onChange={(inView, entry) => {
                            this.setState({ guiderOpen: inView })
                            console.log(inView)
                        }}>


                            <CSSTransition
                                in={this.state.guiderOpen && !this.props.navBarSmallView}
                                timeout={1000}
                                classNames="right-to-left"
                                unmountOnExit
                            >

                                <Fragment>

                                    <img src={this.props.divisionData.imgUrl} className='divisionImg ' alt="" />

                                </Fragment>



                            </CSSTransition>
                        </InView>

                    </div>
                    <div className='col-12 center smallrefCont flex-column'>
                        <div className="col-10 center oHidd position-relative row widthForMoreLoader">
                            {Prods.slice(0, 5).map(i =>
                                <SmallImageRef imgUrl={i.imgUrl} title={i.title}></SmallImageRef>)
                            }
                            {this.state.more && Prods.slice(5).map(i =>
                                <CSSTransition
                                    in={this.state.more}
                                    classNames="opac"
                                    timeout={100}

                                >
                                    <SmallImageRef imgUrl={i.imgUrl} title={i.title}></SmallImageRef>

                                </CSSTransition>


                            )}
                        </div>



                        <CSSTransition
                            in={!this.state.more && Prods.slice(5).length != 0}
                            classNames="opac"
                            timeout={100}
                            unmountOnExit

                        >

                            <Button className='redT' onClick={() => this.setState({ more: true })}><ExpandCircleDownIcon> </ExpandCircleDownIcon>  &nbsp; </Button>
                        </CSSTransition>


                            <CSSTransition
                                in={this.state.more && Prods.slice(5).length != 0}
                                classNames="opac"
                                timeout={100}
                                unmountOnExit

                            >


                                <Button className='redT' onClick={() => this.setState({ more: false })}><ExpandLessIcon> </ExpandLessIcon>  &nbsp; </Button>
                            </CSSTransition>















                    </div>
                </div >
            )
        }
    }
}

const mapStateToProps = (state) => {
    return {
        navBarSmallView: state.navBarSmallView,


    }
}

const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Section)