

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Button } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { setAuthStatusOpen, setAuthStatusData, setLogInStatus, reduxualBarClose, reduxualBarOpen } from '../redux/actions';
import LinearProgress from '@mui/material/LinearProgress';
import { Cookies } from 'react-cookie';
import { Fragment } from 'react';
import ActiveOrds from './ActiveOrds';

function mapStateToProps(state) {

    return {
        authData: state.authData,
        reduxualMessageBarOpen: state.reduxualMessageBarOpen,
        reduxualMessageBar1: state.reduxualMessageBar1,
        reduxualMessageBar2: state.reduxualMessageBar2,
        navBarSmallView: state.navBarSmallView

    };
}





const dispatchToProps = {

    setAuthStatusOpen: setAuthStatusOpen,
    setAuthStatusData: setAuthStatusData,
    setLogInStatus: setLogInStatus,
    setAuthStatusOpen: setAuthStatusOpen,
    reduxualBarOpen: reduxualBarOpen,
    reduxualBarClose: reduxualBarClose


}

class AuthStatus extends Component {
    c = new Cookies()

    state = {
        authData: null
    }


    showSuccessAlert = (message, subMessage) => {
        this.props.reduxualBarOpen(message, subMessage)

        setTimeout(this.props.reduxualBarClose, 3000)
    }



    handleLogOut = () => {
        fetch("https://pardehmahoor.ir/digitalAssets/LogOut")
            .then(res => res.json())
            .then(data => console.log(data))
            .then(this.props.setLogInStatus(false))
            .then(this.props.setAuthStatusOpen(false))
            .then(() => this.c.remove("sessionId"))
            .then(() => this.showSuccessAlert("    "))
    }

    componentDidMount() {

        const sessionId = this.c.get("sessionId")

        fetch("https://pardehmahoor.ir/digitalAssets/getAllUserInfo", {
            method: "POST",
            body: JSON.stringify({
                userId: sessionId
            })
        })
            .then(res => res.json())
            .then(data => {
                this.props.setAuthStatusData(data)
            })
            .then(console.log("sucer tendt to execute"))
    }



    render() {
        return (
            <div className={`punchIn authStatusCont center p-3 `}>
                <Button className='closeBtnAuthStatus' onClick={() => this.props.setAuthStatusOpen(false)}><CloseIcon></CloseIcon></Button>
                {this.props.authData != null &&
                    <Fragment>
                        <div className="col-12 center flex-column   position-relative paymentStatsCont  align-items-start justify-content-between flex-column authContExt h-100 whitT">
                            <div className="col-12 center pb-3 border-bottom topauthcont">
                                <p className='whteShadowedT'>    <strong className='redT'>{this.props.authData.firstname}</strong> . </p>
                            </div>
                            <div className="col-12 p-2 authContent">
                                <p className='whteShadowedT pt-3 pb-3 border-bottom'> -     <strong className='redT'> {this.props.authData.mobile} </strong>    .</p>
                                <p className='whteShadowedT pt-3 pb-3 border-bottom'> -     <strong className='redT'> 0 </strong>       .</p>
                                <p className='whteShadowedT pt-3 pb-3 border-bottom'> -     <strong className='redT'> {this.props.authData.ords.length} </strong>     .</p>
                                <div className="punchIn col-12 center flex-column p-5">
                                    <p className='whteShadowedT pt-3 pb-3 redT'> -        :</p>
                                    {this.props.authData.ords.length == 0 &&
                                        <p className='redT'>    .</p>
                                    }
                                    {this.props.authData.ords.length != 0 &&
                                        this.props.authData.ords.map((i, index) =>
                                            <ActiveOrds data={i} index={index}></ActiveOrds>
                                        )
                                    }

                                </div>
                            </div>

                            <div className="col-12 center justify-content-end  pt-3 border-top bottomAuthCont">
                                <Button onClick={this.handleLogOut} className='whiteT redBg' variant="contained">  </Button>
                            </div>

                        </div>
                    </Fragment>


                }
                {this.props.authData == null &&
                    <div className="col-3">
                        <LinearProgress color="inherit" />
                    </div>
                }
            </div>
        );
    }
}

export default connect(
    mapStateToProps, dispatchToProps
)(AuthStatus);