import React, { Component } from 'react'
import { connect } from 'react-redux'
import Product from './Product'
import Footer from './Footer'
import { Link, withRouter } from 'react-router-dom'
export class Categories extends Component {

    componentDidMount() {
        this.props.goTop()
        if (this.props.match.params.id == "") {

            document.title = `${"   "}`
        }
    }
    render() {
        // const { id, subId } = this.props.match.params;
        const subid = this.props.match.params.subid

        const id = this.props.match.params.id
        var specificProds = this.props.productData.filter(i => i.divisions === this.props.match.params.id)

        console.log(specificProds)
        if (!subid && id === "") {
            specificProds = this.props.productData

        }
        console.log(specificProds)


        if (subid && id == "") {
            specificProds = this.props.productData.filter(i => i.subDivisions == subid)
            console.log("runs runs runs")

        }
        console.log(specificProds)

        if (subid) {

            console.log("***************************", subid)
            console.log("***************************", id)
            specificProds = specificProds.filter(i => i.subDivisions == subid)
        }

        console.log(specificProds)

        console.log(!subid)


        return (
            <div className='col-12 center position-relative flex-column align-items-start justify-content-start dirLtr'>

                <div className='col-12 center categoryInformation sFix'>
                    <div className='center justify-content-end border-bottom flex-row col-10 col-xl-8 mb-3 paddTopBott backWhite categoryInfo'>

                        <div className='categoryBtnContainer row'>


                            {subid &&
                                <Link className='navButton' exact to={`/category/${this.props.match.params.id}/${this.props.match.params.subid}/`} >/{subid}</Link>
                            }

                            {id &&
                                <Link className='navButton' exact to={`/category/${this.props.match.params.id}/`} >/{this.props.match.params.id}</Link>
                            }

                            <Link className='navButton' exact to="/">/</Link>


                        </div>
                        <h2 className='boldhead'>{this.props.match.params.id}</h2>
                    </div>



                </div>
                {this.props.navBarSmallView &&
                    <div className="col-12 center marginalTopContent ">
                        <div className='categoryCard'>
                            <div className='col-12 border-bottom p-1 '>
                                <h2>  </h2>
                            </div>
                            {
                                specificProds[0] &&
                                <div className='p-3 col-12  cardItemCont d-flex row center  '><p> <i className='regularSpan'>{specificProds.length}</i>     <i className='divisionColoring'>{specificProds[0].divisions}</i> </p></div>
                            }
                        </div>
                    </div>
                }

                {specificProds.length == 0 && <div className='NoProd'>   </div>}
                {specificProds.length != 0 &&
                    <div className='ProdInfoParent col-12 center flex-column justify-content-start p-0 p-xl-5 '>
                        <div className="col-10 col-xl-6 center marginalTopContent">
                            <div className='col-12 border borderRadu52 d-flex row oHidd'>
                                {
                                    specificProds.map(i =>
                                        <div className='col-12 col-xl-6 mar-0 padding-0'>
                                            <Product title={i.title} price={i.price} img={i.imgUrl} data={i} />
                                        </div>
                                    )

                                }

                            </div>
                        </div>







                        {!this.props.navBarSmallView &&

                            <div className='col-3 position-absolute fixing h-100 pl-0'>
                                <div className='categoryCard'>
                                    <div className='col-12 border-bottom p-1 '>
                                        <h2>  </h2>
                                    </div>
                                    <div className='p-3 col-12  cardItemCont d-flex row center  '><p> <i className='regularSpan'>{specificProds.length}</i>     <i className='divisionColoring'>{specificProds[0].divisions}</i> </p></div>
                                </div>

                            </div>
                        }

                    </div>
                }
            </div>



        )
    }
}

const mapStateToProps = (state) => {
    return {
        navBarSmallView: state.navBarSmallView
    }
}

const mapDispatchToProps = {}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Categories))