

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Fragment } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { setShoppingCartStages } from '../redux/actions'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { Cookies } from 'react-cookie'
import { Button } from '@mui/material'
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import { CSSTransition } from 'react-transition-group';


export class FinallizatorCart extends Component {
    constructor() {
        super()
        this.c = new Cookies()
    }


    state = {

        sessionId: null,
        showAlert: false,
        alertMessage: ""

    }


    showAlert = (message, subMessage) => {
        this.setState({
            showAlert: true,
            alertMessage: message
        })

        setTimeout(() => this.setState({ showAlert: false, alertMessage: "" }), 5000)
    }




    handleClick = () => {

        try {

            const id = this.c.get("sessionId")
            if (typeof id === "undefined") {

                this.showAlert("      ")
                setTimeout(() => this.props.history.push("/Login"), 3000)
                return
            }
        } catch {
            this.showAlert("      ")

        }


        this.props.setShoppingCartStages({
            confirm: false,
            chooseAddress: true,
            presonalInformation: false

        })
    }


    render() {


        // Mobile:
        if (this.props.navBarSmallView) {
            return (

                <Fragment>

                <div className='finalizasator finalizasatorMobileView center '>

                    <div className='col-11 d-flex flex-column center'>
                        {this.props.totalPrice != 0 &&
                            <Fragment>

                                <div className='p-0 mt-2 center justify-content-end'>
                                    <div className=''><p className='priceFont redT'> {this.props.totalPrice} </p></div>
                                </div>


                                <Button onClick={this.handleClick} variant="contained" className="finalizasatorBtn finalizasatorBtnMobile center"><p>  <ChevronLeftIcon></ChevronLeftIcon></p></Button>
                            </Fragment>

                        }
                        {this.props.totalPrice == 0 &&

                            <div className='p-4 center justify-content-end col-12'>
                                <div className=' col-12 center'><p className='priceFont pricingFont col-4'>!   </p></div>
                            </div>

                        }
                    </div>

                </div>
                    <CSSTransition
                        in={this.state.showAlert}
                        classNames="opac"
                        timeout={300}
                        unmountOnExit
                    >

                        <div className="col-12 alertCont dirRtl">
                            <Alert severity="error">
                                <AlertTitle>  !</AlertTitle>
                                <strong>{this.state.alertMessage}</strong>
                            </Alert>
                        </div>
                    </CSSTransition>

                </Fragment>

            )
        }



        // Desktop:
        if (!this.props.navBarSmallView) {

            return (
                <div className='center flex-column'>


                    <div className='finalizasator'>
                        <div className='topPartShop'></div>
                        <div className='p-5 center justify-content-end'>
                            <div className='border-top'><p className='priceFont'> {this.props.totalPrice} </p></div>
                        </div>
                        <div className='col-12 paddingForPoints paddingForPoints center justify-content-end'>
                            <p>    <i className='redDot'></i></p>
                        </div>
                        <div className='col-12  center paddingForPoints justify-content-end'>
                            <p>   10% (   )<i className='redDot'></i></p>
                        </div>

                    </div>

                    <Button onClick={this.handleClick} variant="contained" className="finalizasatorBtn center"><p>  <ChevronLeftIcon></ChevronLeftIcon></p></Button>


                    <CSSTransition
                        in={this.state.showAlert}
                        classNames="opac"
                        timeout={300}
                        unmountOnExit
                    >

                        <div className="col-12 alertCont">
                            <Alert severity="error">
                                <AlertTitle>  !</AlertTitle>
                                <strong>{this.state.alertMessage}</strong>
                            </Alert>
                        </div>
                    </CSSTransition>

                </div>

            )
        }
    }
}

const mapStateToProps = (state) => {
    return {
        totalPrice: state.buyBasket.totalPrice,
        navBarSmallView: state.navBarSmallView
    }
}

const mapDispatchToProps = {
    setShoppingCartStages: setShoppingCartStages
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FinallizatorCart))