

import React, { Component } from 'react'
import { connect } from 'react-redux'
import Openion from './Openion'
import Footer from './Footer'
import Product from './Product'
import AddToCart from './AddToCart'
import { setNavMenuState } from '../redux/actions'
import { withRouter } from 'react-router-dom'
import { Link } from 'react-router-dom/cjs/react-router-dom'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ControlPointDuplicateIcon from '@mui/icons-material/ControlPointDuplicate';
import { Fragment } from 'react'
import { Button } from '@mui/material'
import { Cookies } from 'react-cookie'
import ImageLargeViewComponent from './imageLargeViewComponent'
import { ThreeDRotationSharp } from '@mui/icons-material'

export class ProductInfo extends Component {
    constructor() {
        super()
        this.appRef = React.createRef()
        this.openionRef = React.createRef()
    }



    state = {
        cMessage: "",
        commentData: null,
        imageData: null,
        bigImageShowOpen: false
    }

    commentDataAqusition = (djangoId) => {
        console.log("started . . . .")
        fetch("https://pardehmahoor.ir/digitalAssets/getComments", {
            method: "POST",
            body: JSON.stringify({ djangoId: djangoId })
        })
            .then(r => r.json())
            .then(data => {
                this.setState({ commentData: data.data })
                console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", data)
            })
    }


    handleCommentInpChange = e => {
        this.setState({ cMessage: e.target.value })
    }

    c = new Cookies()
    scrollHandler = () => {
        console.log(this.appRef.current.scrollTop)
        if (Number(this.appRef.current.scrollTop) < 100 && this.props.navMenoOpen) {

            this.props.setNavMenuState(false)
            return
        }


        if (Number(this.appRef.current.scrollTop) > 100 && !this.props.navMenoOpen) {

            this.props.setNavMenuState(true)
            return
        }
    }

    componentDidMount() {

        const specificData = this.props.productData.filter((i) => i.title == this.props.match.params.id)[0]
        document.title= ` ${specificData.title}    ${specificData.divisions}`
        this.setState({ imageData: specificData.prodImgUrl })
        this.commentDataAqusition(specificData.djangoId)

        this.props.goTop()

    }


    switchPics = (p) => {
        console.log("$$$$$$$$$$$$$$$$$$$$$$$$$", p)
        var d = this.state.imageData
        const toAddToRight = d[0]
        d.splice(p, 1)
        d.push(toAddToRight)
        this.setState({ imageData: d })
    }


    submitAComment = (prodId) => {
        console.log("commentSubmitted ")
        fetch("https://pardehmahoor.ir/digitalAssets/submitAComment", {
            method: "POST",
            body: JSON.stringify({ id: this.c.get("sessionId"), prodId: prodId, message: this.state.cMessage })
        })
            .then(r => r.json())
            .then(d => console.log(d))
    }


    handleOpenionRefView = () => {
        this.openionRef.current.scrollIntoView({ behavior: "smooth" })
    }

    switchBigImageShow = () => {
        this.setState({ bigImageShowOpen: !this.state.bigImageShowOpen })
    }



    render() {
        console.log(this.props.productData)
        console.log(this.props.match.params)
        const specificData = this.props.productData.filter((i) => i.title == this.props.match.params.id)[0]

        const prodsWithSamedivision = this.props.productData.filter((i) => i.divisions == specificData.divisions)


        // console.log(specificData)
        return (

            <Fragment>

                {this.state.imageData && this.state.bigImageShowOpen &&
                    <ImageLargeViewComponent switchBigImageShow={this.switchBigImageShow} imgurl={this.state.imageData[0]}></ImageLargeViewComponent>
                }
                <div className='ProdInfoParent col-12 center flex-column justify-content-start p-0 pt-xl-5 ' ref={this.appRef} onScroll={this.scrollHandler}>
                    <div className='col-12 center position-relative flex-column flex-xl-row align-items-start justify-content-start'>
                        <div className='col-12 col-xl-5 center flex-column'>
                            {this.state.imageData &&
                                this.props.navBarSmallView &&
                                <div className="col-12 center">
                                    <Button onClick={this.switchBigImageShow} className='prodImg' >
                                        <img src={this.state.imageData[0]} className='col-12 h-100' alt={specificData.title} />
                                    </Button>
                                </div>
                            }

                            <div className='col-12 center row m-2'>
                                {
                                    !this.props.navBarSmallView && this.state.imageData &&
                                    <Button onClick={this.switchBigImageShow} className='prodImg' >
                                        <img src={this.state.imageData[0]} className='col-12 h-100' alt={specificData.title} />
                                    </Button>
                                }

                                {this.state.imageData && this.state.imageData.slice(1).map(i =>
                                    <Button className='imgBtn center ' onClick={() => this.switchPics(i)}><img className='col-12 h-100' src={i} /></Button>
                                )}


                            </div>
                        </div>

                        <div className={`col-12 col-xl-4  d-flex justify-content-start align-items-start flex-column p-5 p-xl-2 ${this.props.navBarSmallView && "pt-2 pb-0"}`}>

                            <div className=' col-12 '>
                                <h1 className='boldText titleFontSize' >{specificData.title}</h1>

                                <div className="col-12 row m-1 mt-3 p-3">

                                    <Link className='navButton' exact to="/"></Link>
                                    <Link className='navButton' exact to={`/category/${specificData.divisions}`} >/{specificData.divisions}</Link>
                                    {specificData.subDivisions != "None" &&
                                        <Link className='navButton' exact to={`/category//${specificData.subDivisions}`} >/{specificData.subDivisions}</Link>
                                    }

                                </div>

                                <div className="position-relative col-12">

                                    <div className='col-12 specAndViewCont  center'>
                                        <button onClick={this.handleOpenionRefView} className='view prodsHeading '>  <KeyboardArrowDownIcon></KeyboardArrowDownIcon></button>
                                        <div className='col-10 '>

                                            <h3><i className='specifyDivision'></i>   </h3>
                                            <div className='specActualCont'>
                                                {specificData.features.map(i =>

                                                    <p><i className='redDot' /> {i} </p>
                                                )}

                                            </div>
                                            <div className='col-12 intro  '>

                                                <h3><i className='specifyDivision'></i> </h3>
                                                <div className='specActualCont'>
                                                    <p>{specificData.introduction}</p>
                                                </div>
                                            </div>
                                        </div>


                                    </div>

                                </div>

                            </div>



                            {!this.props.navBarSmallView &&

                                <div className='col-3 p-0 pl-4  position-absolute shoppingBoxPosFix d-flex justify-content-start align-items-start'>

                                    <AddToCart price={specificData.price} data={specificData}></AddToCart>
                                </div>
                            }

                        </div>


                    </div>
                    <div className='col-12 col-xl-8 d-flex flex-column specsWrapper center'>
                        <div className={`col-10 p-5 p-xl-2 col-xl-12 ${this.props.navBarSmallView && "pt-0"}`}>

                            <div className='col-12 d-flex align-items-center'>

                                <h3><i className='specifyDivision'></i></h3>
                                <p className='PrdouctTitleSmall'>{specificData.title}</p>
                            </div>

                            <div className='col-12 p-2 p-xl-5 specsCont'>
                                {specificData.specifics.map(i =>
                                    <p> <ControlPointDuplicateIcon className='fa fa-plus redPlus'></ControlPointDuplicateIcon> {i}</p>
                                )}

                            </div>

                        </div>

                    </div>
                    <div className='dirFix col-12'>




                        <div className=' col-12 OpenionsSection  '>

                            <h2 className='boldText boldText p-4'> </h2>
                        </div>

                        <div className={`col-12 ${!this.props.navBarSmallView && "introOutside"} center flex-column `}>


                            <div className='col-12  rowPadMarNone d-flex flex-row  justify-content-start punchIn mayLikeCont' >


                                {this.state.imageData && prodsWithSamedivision.map(i =>
                                    <div className='sectionProdCont'>
                                        <Product title={i.title} price={i.price} img={i.imgUrl} data={i}/>
                                    </div>
                                )}

                            </div>

                        </div>
                    </div>



                    <div className='col-12 row mt-5'>



                    </div>

                    <div className='col-12 center '>
                        <div className='col-11 col-xl-6 border-top position-relative OpenionsSection mt-5 ' ref={this.openionRef}>
                            <h2 className='boldText openionSectNameCont '> </h2>

                            <div className='userOpCont'>
                                {this.state.commentData != null &&
                                    this.state.commentData.map(i =>
                                        <Openion data={i} ></Openion>
                                    )

                                }
                            </div>

                            {this.props.isLoggedIn &&
                                <Fragment>

                                    <div className='col-12 punchIn position-relative center p-0 commentsSection'>
                                        <h5 className='SubmitACommentHeading'> :</h5>
                                        <input onChange={this.handleCommentInpChange} className='col-10 h-100 commentInput d-flex d-flex justify-content-start align-items-start'></input>
                                    </div>

                                    <div className='col-12 center'>
                                        <Button className='col-10 commentButton' onClick={() => this.submitAComment(specificData["djangoId"])}>   </Button>
                                    </div>
                                </Fragment>
                            }
                        </div>




                        {!this.props.navBarSmallView &&

                            <div className='col-5
                             p-5 pt-0 d-flex justify-content-end'>

                                <AddToCart price={specificData.price} data={specificData}></AddToCart>
                            </div>
                        }

                    </div>
                </div>
                {this.props.navBarSmallView &&

                    <AddToCart price={specificData.price} data={specificData}></AddToCart>
                }
            </Fragment>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        navMenoOpen: state.navMenoOpen,
        navBarSmallView: state.navBarSmallView,
        productData: state.productData,
        isLoggedIn: state.isLoggedIn

    }
}


const mapDispatchToProps = {
    setNavMenuState: setNavMenuState
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ProductInfo))