import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setSliderData } from '../redux/actions'
import { Link, withRouter } from 'react-router-dom'
import { Fragment } from 'react'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { Skeleton } from '@mui/material'
import LazyLoad from 'react-lazyload';


export class PhotoSlider extends Component {

  componentDidMount() {

    // setInterval(this.goRight, 2000)

  }



  goRight = () => {
    // console.log("!!!!!!!!!!!!!!!", this.props.sliderData)


    try {

      var arr = this.props.sliderData
      var initItem = arr[0]
      var first = arr.slice(1, ((arr.length) / 2) + 1)
      var second = arr.slice(arr.length - ((arr.length - 1) / 2))

    first.splice(0, 0, initItem)
    second.splice(second.length, 0, first.pop())
    initItem = second.shift()
    
    arr = [...[initItem], ...first, ...second]

    this.props.setSliderData(null)
    this.props.setSliderData(arr)
    console.log("runs")
  }
  catch {
    console.log()
  }
    
  }




  render() {
    var firstHalf = null
    var original = null
    var secondHalf = null
    if (this.props.sliderData != null) {

      var original = this.props.sliderData
      var firstHalf = this.props.sliderData.slice(1, ((original.length) / 2) + 1)
      var secondHalf = this.props.sliderData.slice(original.length - ((original.length - 1) / 2))

    }

    return (


      <div className='photoSliderContainer position-relative center o-hidd'>
        {/* <button onClick={this.goRight} className="navButton" ></button> */}

        {this.props.sliderData != null &&





          <Fragment>



            <Link className="imageContainerLink" target="_blank" style={{ zIndex: `${original.length}` }} exact to={`/${original[0].route}`} >
              {!original[0].img && <Skeleton variant="rectangular" width={2100} height={200} />}

              {original[0].img && <LazyLoad className='h-100 col-12' ><img src={original[0].img} onChange={() => console.log("Change")} className='col-12 mainImgHeight' alt="" /></LazyLoad>
              }
            </Link>



            {firstHalf.map(i =>


              // <CSSTransition
              //   key={i.route}
              //   // in={this.props.sliderData}

              //   classNames="specialV2"
              //   timeout={300}

              // >

              <Link target="_blank" className="imageContainerLink rightBasic " exact to={`/${i.route}`} style={{
                left: `${(firstHalf.indexOf(i)) * -9}vw`,
                scale: `${100 - (original.indexOf(i) * 20)}%`,
                zIndex: `${original.length - 1 - firstHalf.indexOf(i)}`


              }}>
                {!i.img && <Skeleton variant="rectangular" width={2100} height={200} />}
                {i.img && <LazyLoad className='h-fit col-12'><img src={firstHalf.indexOf(i)<2 && i.img} className=' col-12 mainImgHeight' alt="" /></LazyLoad>}</Link>



            )}

            {secondHalf.map(i =>


              // <CSSTransition
              //   // in={this.props.sliderData}

              //   key={i.route}
              //   classNames="specialV2"
              //   timeout={300}

              // >

              <Link target="_blank" className="imageContainerLink rightBasic " exact to={`/${i.route}`} style={{
                right: `${(secondHalf.indexOf(i)) * -9}vw`,
                scale: `${100 - ((original.indexOf(i) - secondHalf.length) * 20)}%`,
                zIndex: `${original.length - 1 - secondHalf.indexOf(i) - secondHalf.length}`


              }}><LazyLoad offset={100} className='h-fit col-12'><img src={secondHalf.indexOf(i)<2 && i.img} className='col-12 mainImgHeight' alt="" /></LazyLoad></Link>
              // </CSSTransition>


            )}




          </Fragment>




        }




      </div>

    )
  }
}

const mapStateToProps = (state) => {
  return {
    sliderData: state.sliderData,

  }
}

const mapDispatchToProps = {
  setSliderData: setSliderData
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PhotoSlider))