import React, { Component } from 'react'

import { ReactComponent as Logo } from '../assets//MahoorLogoType.svg';
import { connect } from 'react-redux'
import { setNavMenuState, setSearchData, setLogInStatus, setAuthStatusOpen, reduxualBarClose, reduxualBarOpen } from '../redux/actions'
import ShoppingBix from './ShoppingBix'
import NavItemCont from './NavItemCont'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { Link, withRouter } from 'react-router-dom'
import { Fragment } from 'react'
import ShoppingCartSharpIcon from '@mui/icons-material/ShoppingCartSharp';
import SearchSharpIcon from '@mui/icons-material/SearchSharp';
import CloseIcon from '@mui/icons-material/Close';
import { Button } from '@mui/material'
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import LogoutIcon from '@mui/icons-material/Logout';
import PersonPinIcon from '@mui/icons-material/PersonPin';
import { Cookies } from 'react-cookie'
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import AutoAwesomeMotionIcon from '@mui/icons-material/AutoAwesomeMotion';

export class NavBar extends Component {
    constructor() {
        super()
        this.c = new Cookies()
    }
    state = {
        menoOpen: false,
        showAlert: false,
        alertMessage: "",
        alertMessage2: ""
    }


    showSuccessAlert = (message, subMessage) => {
        this.props.reduxualBarOpen(message, subMessage)
        setTimeout(this.props.reduxualBarClose, 3000)
    }



    handleLogOut = () => {
        fetch("https://pardehmahoor.ir/digitalAssets/LogOut")
            .then(res => res.json())
            .then(data => console.log(data))
            .then(() => this.props.setLogInStatus(false))
            .then(() => this.c.remove("sessionId"))
            .then(() => this.showSuccessAlert("    "))
    }


    navToggle = () => {
        this.props.setNavMenuState(!this.props.navMenoOpen)
    }

    navSearch = (e) => {
        if (e.target.value === "") {
            this.props.setSearchData(null)
            return
        }


        fetch(`https://pardehmahoor.ir/digitalAssets/search?searchQuerry=${e.target.value}`)
            .then(res => res.json())
            .then(data => this.props.setSearchData(data.Value))
    }


    handleClick = () => {
        this.setState({ menoOpen: !this.state.menoOpen })
    }
    closeMenu = () => {
        this.setState({ menoOpen: false })
    }

    hanleAuthStatus = () => {
        this.props.setAuthStatusOpen(!this.props.isAuthOpen)
    }

    resetDTitle = () => {
        document.title = " |       "
    }

    handleLogoClick = () => {
        this.props.goTop()
        this.resetDTitle()
    }

    render() {
        return (

            <Fragment>

                <nav className={`d-flex flex-column `}>

                    <div className={`col-12 paddingFixForNav center flex-row row navShadow`}>
                        <div className={`col-11 d-flex flex-row center ${!this.props.navBarSmallView && "justify-content-between"}`}>


                            <div className={`col-12 center justify-content-start ${this.props.navBarSmallView && "col-12"}`}>
                                <div className='col-12 d-flex flex-row center '>

                                    {!this.props.navBarSmallView &&
                                        <div className=' h-100 center '>
                                            <Link to="/" onClick={this.handleLogoClick} className="logoContainer center" > <Logo className='mahoorLogo' /></Link>
                                        </div>

                                    }

                                    {/* <Link to="/"  ><h1 className='logoCont '>YOUR BUSINESS LOGO</h1></Link>
                                     */}
                                    <input type="text" className='searchBar  p-3' placeholder='    ' onChange={this.navSearch} />

                                    {!this.props.navBarSmallView &&
                                        <div className=' d-flex inButtonContainer d-flex flex-row'>
                                            <div className='h-100  col-12 items center justify-content-start flex-row '>
                                                <Link className="ShoppingBox center position-relative " to="/shoppingCart">
                                                    <ShoppingCartSharpIcon></ShoppingCartSharpIcon>
                                                    <span className='spanForShoppingBox'>
                                                        {this.props.data.length}
                                                    </span>

                                                </Link>
                                                {/* <div className="mr-5"></div> */}
                                                <div className='widthFit row navLoginBtnCont center mr-5'>
                                                    {!this.props.isLoggedIn &&
                                                        <Fragment>
                                                            <button className='navButton heightFixNavLogin'><Link className="redT col-12 h-100" to="/SignUp">   </Link></button>
                                                            <button className='navButton heightFixNavLogin'><Link className="redT col-12 h-100" to="/Login">  </Link></button>
                                                        </Fragment>
                                                    }
                                                    {this.props.isLoggedIn &&
                                                        <Fragment>
                                                            <button className='navButton heightFixNavLogin redT' onClick={this.hanleAuthStatus}> <PersonPinIcon></PersonPinIcon> </button>
                                                        </Fragment>
                                                    }

                                                </div>






                                            </div>
                                        </div>}
                                </div>
                            </div>




                        </div>
                    </div>


                    <CSSTransition
                        in={!this.props.navMenoOpen && !this.props.navBarSmallView}
                        classNames="BottomToTop"
                        timeout={200}
                        unmountOnExit
                    >


                        <div className={`col-12   d-flex row secondPart center ${this.props.navMenoExtended && ""} ${this.props.navBarSmallView && "justify-content-start"} `}>

                            <div className={`col-8 center row navBtnWrapper ${this.state.menoOpen && "navBtnWrapperOpenMenu"}`}>


                                {this.props.navBarSmallView && !this.state.menoOpen &&
                                    <Button className='NavBarMenuOpen' onClick={() => this.setState({ menoOpen: true })}> <MenuIcon></MenuIcon></Button>
                                }

                                {this.props.navBarSmallView && this.state.menoOpen &&
                                    <Button onClick={() => this.setState({ menoOpen: false })}> <CloseIcon></CloseIcon></Button>
                                }


                                {((this.props.divisionData != null && !this.props.navBarSmallView) || (this.state.menoOpen)) &&
                                    this.props.divisionData.map(i =>
                                        <Button className={`navButtonV2 ${this.state.menoOpen && "wFull"} `}><Link className={`col-12 h-100 center grayText ${this.props.navBarSmallView && "justify-content-start m-1"}`} to={`/category/${i}`}> <i className='boldMore navBarBtnContent'>{i}</i></Link></Button>
                                    )}



                            </div>



                            <Link className='navButton about'> </Link>


                        </div>

                    </CSSTransition>







                    {Array.isArray(this.props.searchResults) &&
                        <NavItemCont />
                    }



                    <CSSTransition
                        in={this.props.navBarSmallView}
                        classNames="BTT"
                        timeout={450}
                        unmountOnExit
                    >
                        <div className='bottomNavigator center justify-content-between d-flex flex-column'>
                            <div className="col-12 d-flex flex-column center">
                                <div className="col-12 d-flex flex-column">


                                    <CSSTransition
                                        in={this.state.menoOpen && this.props.navBarSmallView && this.props.divisionData != null}
                                        classNames="right-to-left"
                                        timeout={450}
                                        unmountOnExit
                                    >
                                        <Fragment>



                                            <div className="col-12 pt-3 d-flex justify-content-between">
                                                <p className='center itemsHeadText'>  </p>
                                                <Button className='retT closeBtnForMobileNav' onClick={this.handleClick}><CloseIcon className='retT'></CloseIcon></Button>
                                            </div>

                                            <div className={`col-12  ${this.state.menoOpen && "p-3 pt-0 divisionWrapper"}`}>


                                                <CSSTransition
                                                    in={this.props.divisionData != null}
                                                    classNames="opac"
                                                    timeout={450}
                                                    unmountOnExit
                                                >
                                                    <Fragment>

                                                        {this.props.divisionData != null &&
                                                            this.props.divisionData.map(i =>
                                                                <Button onClick={this.handleClick} className={`navButtonV2    ${this.state.menoOpen && "wFull"} `}> <AutoAwesomeMotionIcon className='redT' /> <div className='m-1' /> <Link className={`col-12 h-100 center grayText divisionsMBtn  ${this.props.navBarSmallView && "justify-content-start m-1"}`} to={`/category/${i}`}> <i className='boldMore navBarBtnContent divisionMColor'>{i}</i></Link></Button>

                                                            )}
                                                    </Fragment>

                                                </CSSTransition>
                                            </div>
                                        </Fragment>
                                    </CSSTransition>

                                </div>
                            </div>
                            <div className="col-12  center justify-content-between bottomNavItems flex-row">
                                <div className=' center w-fit flex-column'>

                                    <Button onClick={this.closeMenu} className=' h-100 center  d-flex flex-column m-0 relativePadding'>
                                        <Link to="/" className=" h-100 center  d-flex flex-column m-0 redT" >
                                            <HomeIcon></HomeIcon>

                                        </Link>
                                    </Button>
                                </div>
                                {/* onClick={this.setState({ menoOpen: !this.state.menoOpen })} */}

                                <div className=' center  flex-column'>
                                    <Button onClick={this.handleClick} className=' h-100 center  d-flex flex-column m-0 redT relativePadding' >

                                        <MenuIcon></MenuIcon>

                                    </Button>
                                </div>

                                {!this.props.isLoggedIn &&
                                    <div className='  center flex-column'>
                                        <Button onClick={this.closeMenu} className='ShoppingBox m-0  flex-column relativePadding'>
                                            <Link to="/Login" className=" h-100 center  d-flex flex-column redT">
                                                <AccountCircleIcon></AccountCircleIcon>

                                            </Link>
                                        </Button>
                                    </div>
                                }

                                {this.props.isLoggedIn &&
                                    <div className=' center  flex-column'>

                                        <Button className='ShoppingBox m-0 redT flex-column relativePadding' onClick={this.hanleAuthStatus}> <PersonPinIcon></PersonPinIcon>

                                        </Button>

                                    </div>
                                }



                                <div className=' center  flex-column'>
                                    <Link onClick={this.closeMenu} className="ShoppingBoxMobile center position-relative " to="/shoppingCart">
                                        <ShoppingCartSharpIcon></ShoppingCartSharpIcon>
                                        <span className='spanForShoppingBox'>
                                            {this.props.data.length}
                                        </span>

                                    </Link>
                                </div>



                            </div>
                        </div>
                    </CSSTransition>


                    {!this.props.navBarSmallView &&

                        <div className="navSpacer"></div>
                    }

                </nav>

                <CSSTransition
                    in={this.state.showAlert}
                    classNames="opac"
                    timeout={300}
                    unmountOnExit
                >

                    <div className="col-12 alertCont">
                        <Alert severity="success" variant='filled'>
                            <AlertTitle></AlertTitle>
                            {this.state.alertMessage}  <strong> {this.state.alertMessage2}</strong>
                        </Alert>
                    </div>
                </CSSTransition>

            </Fragment >

        )
    }
}

const mapStateToProps = (state) => {
    return {
        navMenoOpen: state.navMenoOpen,
        divisionData: state.divisionData,
        data: state.buyBasket.prodData,
        searchResults: state.searchResults,
        navBarSmallView: state.navBarSmallView,
        navMenoExtended: state.navMenoExtended,
        isLoggedIn: state.isLoggedIn,
        isAuthOpen: state.isAuthOpen,
        reduxualMessageBarOpen: state.reduxualMessageBarOpen,
        reduxualMessageBar1: state.reduxualMessageBar1,
        reduxualMessageBar2: state.reduxualMessageBar2



    }
}

const mapDispatchToProps = {
    setNavMenuState: setNavMenuState,
    setSearchData: setSearchData,
    setLogInStatus: setLogInStatus,
    setAuthStatusOpen: setAuthStatusOpen,
    reduxualBarOpen: reduxualBarOpen,
    reduxualBarClose: reduxualBarClose


}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(NavBar))