import React, { Component } from 'react'
import { connect } from 'react-redux'
import CircularProgress from '@mui/material/CircularProgress';
import { withRouter, Link } from 'react-router-dom/cjs/react-router-dom.min';
import { Button } from '@mui/material';
import { Cookies } from 'react-cookie';
import { setAuthStatusOpen } from '../redux/actions';
import { reduxualBarOpen, reduxualBarClose } from '../redux/actions';

export class PaymentHandler extends Component {

    c = new Cookies()

    showSuccessAlert = (message, subMessage) => {
        this.props.reduxualBarOpen(message, subMessage)
        setTimeout(this.props.reduxualBarClose, 3000)
    }






    submitAbuy = () => {
        if (!(this.props.match.params.status == 200) || !(this.c.get("buyData"))) {
            return
        }
        const data = this.c.get("buyData")






        fetch("https://pardehmahoor.ir/digitalAssets/submitBuy", {
            method: "POST",
            body: JSON.stringify(data)
        }
        )
            .then(res => res.json())
            .then(this.c.remove("buyData", { path: "/" }))
            .then(this.c.remove("buyBasket", { path: "/" }))
            .then(data => {


                if (data.status == 200) {

                    this.showSuccessAlert("", "    .")
                    // this.c.remove("buyData")
                }

            })


    }

    componentDidMount() {
        this.submitAbuy()
    }
    handleAuthStatus = () => {
        this.props.setAuthStatusOpen(!this.props.isAuthOpen)
    }

    render() {
        const data = this.props.match.params



        if (data.status == 200) {
            return (


                <div className="col-12 p-0 p-xl-5  center  paymentHandlerCont">

                    <div className="col-12 center flex-column  border position-relative paymentStatsCont">
                        <div className="col-12  border h1ForPaymentSuccess">
                            <h1 className=""> </h1>
                        </div>

                        <div className="col-10">
                            <p>     .           </p>
                        </div>

                        <div className="col-12 center flex-column  getOrderInfoBtnWrapper">
                            <Button varient="contained" className="col-10 getOrderInfoBtn" onClick={this.handleAuthStatus}> </Button>
                            <Button varient="contained" className="col-10 getOrderInfoBtn getBackStyle center"><Link className="col-12 h-100 whiteT center" to="/">      </Link></Button>
                        </div>
                    </div>




                </div>

            )

        } else if (data.status == 101) {
            return (


                <div className="col-12 p-0 p-xl-5  center  paymentHandlerCont">

                    <div className="col-12 center flex-column  border position-relative paymentStatsCont">
                        <div className="col-12  border h1ForPaymentRepeatedTransaction">
                            <h1 className=""> </h1>
                        </div>

                        <div className="col-10">
                            <p> ,      .</p>
                        </div>

                        <div className="col-12 center flex-column  getOrderInfoBtnWrapper mt-4">
                            <Button varient="contained" className="col-10 getOrderInfoBtn getBackStyle center"><Link className="col-12 h-100 whiteT center" to="/">      </Link></Button>
                        </div>
                    </div>




                </div>

            )

        }

        else {
            return (


                <div className="col-12 p-0 p-xl-5  center  paymentHandlerCont">

                    <div className="col-12 center flex-column  border position-relative paymentStatsCont">
                        <div className="col-12  border h1ForPaymentFailure">
                            <h1 className=""> </h1>
                        </div>

                        <div className="col-10">
                            <p>     .    .</p>
                        </div>

                        <div className="col-12 center flex-column  getOrderInfoBtnWrapper mt-4">
                            <Button varient="contained" className="col-10 getOrderInfoBtn getBackStyle center"><Link className="col-12 h-100 whiteT center" to="/">      </Link></Button>
                        </div>
                    </div>




                </div>

            )


        }

    }
}

const mapStateToProps = (state) => {
    return {

    }
}

const mapDispatchToProps = {

    setAuthStatusOpen: setAuthStatusOpen,
    reduxualBarOpen: reduxualBarOpen,
    reduxualBarClose: reduxualBarClose
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PaymentHandler))