

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import { setBuyBasket, addToCartDicision, } from '../redux/actions'
import { v4 } from 'uuid'
import { faDirections } from '@fortawesome/free-solid-svg-icons'
import { Cookies } from 'react-cookie'
import { Fragment } from 'react'
import { CSSTransition } from 'react-transition-group'
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import { TextField, Button } from '@mui/material'
import Autocomplete from '@mui/material/Autocomplete';
import ExpandMoreSharpIcon from '@mui/icons-material/ExpandMoreSharp';





export class AddToCart extends Component {
    constructor() {
        super()
        this.cookies = new Cookies()
    }

    state = {
        widthValue: 0,
        width: 0,
        heightValye: 0,
        height: 0,
        chainPosition: "",
        installationPosition: "",
        description: ""
    }


    AddToCart = (data) => {



        this.props.setBuyBasket({
            ...this.props.data,
            id: v4(),
            width: this.state.widthValue,
            height: this.state.heightValye,
            description: this.state.description,
            chainPosition: this.state.chainPosition,
            installationPosition: this.state.installationPosition

        }, (this.props.price / 10000) * this.state.width * this.state.height)
        this.cookies.set("buyBasket", this.props.buyBasket, { path: "/" })

        setTimeout(1000, this.props.history.push('/ShoppingCart'))
    }


    handleWidthChange = e => {
        const val = e.target.value

        if (val == 0) {
            this.setState({ width: 0 })
            return
        }

        if (val <= 100) {
            this.setState({ width: 100, widthValue: val })
            return
        }

        if (this.state.height <= 100) {
            this.setState({ width: 100, widthValue: val })
            return
        }

        


        this.setState({ width: val })
    }

    handleHeightChange = e => {
        const val = e.target.value
        if (val == 0) {
            this.setState({ height: 0 })
            return
        }


        if (val <= 150) {
            this.setState({ height: 150, heightValye: val })
            return
        }
        this.setState({ height: val })
    }



    componentDidMount() {
        if (!this.props.navBarSmallView) {
            this.props.addToCartDicision({
                decisionToBuy: false,
                chooseSizes: true

            })

        }
    }


    render() {
        const data = this.props.data
        const chainPositionOptions = ["", ""]
        const installationPosition = ["", ""]




        // Mobile
        if (this.props.navBarSmallView) {
            return (
                <form type="submit" onSubmit={this.AddToCart} className='addToCartContMobile center p-1 pt-3 border'>
                    <div className='col-12 position-relative center flex-column'>

                        <CSSTransition
                            in={this.props.AddToCartStages.decisionToBuy}
                            timeout={100}
                            classNames="topToBottom"
                            unmountOnExit

                        >

                            <div className='col-12 center  decideToBuyBtnCont'>


                                <div className="col-12 d-flex flex-row justify-content-between">

                                    <Button variant="contained" className='col-6 DecideToShopBtn ' onClick={() => this.props.addToCartDicision({
                                        decisionToBuy: false,
                                        chooseSizes: true

                                    })}> </Button>
                                    <div className='col-6 center  productPriceContainer '> <p className='dirRtl redT'>  &nbsp; {this.props.price.toLocaleString(undefined)} 
                                    </p>  <i className='marginRightWeak'></i> </div>

                                </div>
                            </div>

                        </CSSTransition>


                        <CSSTransition
                            in={this.props.AddToCartStages.chooseSizes}
                            timeout={100}
                            classNames="topToBottom"
                            unmountOnExit

                        >
                            <Fragment>


                                <div className='col-12  mobileAddToCartWrapper pt-4'>
                                    <Button className='arrBackBtnAddToCart' onClick={() => this.props.addToCartDicision({
                                        decisionToBuy: true,
                                        chooseSizes: false

                                    })}><ArrowLeftIcon></ArrowLeftIcon></Button>


                                    <div className="center col-12">

                                        <div className='col-11  p-4  border-bottom center  '> <p className=' fontMedB redT'></p></div>
                                    </div>


                                    <div className="center col-12">
                                        <div className='col-10  p-4  border-bottom'><TextField id="outlined-basic" inputProps={{ color: "white" }} variant="outlined" type="number" required className='sizeInput col-12 mobileSizeInput chooseAddressBtnMobile ' label=' ( )' onChange={this.handleHeightChange} /></div>
                                    </div>

                                    <div className="center col-12">
                                        <div className='col-10 center p-4 border-bottom ' ><TextField id="outlined-basic" variant="outlined" type="number" required className='sizeInput col-12 mobileSizeInput  chooseAddressBtnMobile' label='  ( )' onChange={this.handleWidthChange} /></div>
                                    </div>

                                    <div className="center col-12">
                                        <Autocomplete required value={this.state.selecedVal1} onChange={(event, newValue) => {
                                            this.setState({ chainPosition: newValue })

                                        }} variant='contained' options={chainPositionOptions} className='chooseAddressBtn mobileSizeInput  chooseAddressBtnMobile p-4 border-bottom' renderInput={(params) => <TextField {...params} label="   " />}>  <ExpandMoreSharpIcon></ExpandMoreSharpIcon></Autocomplete>

                                    </div>

                                    <div className="center col-12">
                                        <Autocomplete required value={this.state.selecedVal1} onChange={(event, newValue) => {
                                            this.setState({ installationPosition: newValue })

                                        }} variant='contained' options={installationPosition} className='chooseAddressBtn mobileSizeInput  chooseAddressBtnMobile p-4 border-bottom' renderInput={(params) => <TextField {...params} label="   " />}>  <ExpandMoreSharpIcon></ExpandMoreSharpIcon></Autocomplete>

                                    </div>

                                    <div className="center col-12">
                                        <div className='col-10 center p-4 border-bottom'><TextField id="outlined-basic" variant="outlined" type="text" className='sizeInput col-12 mobileSizeInput chooseAddressBtnMobile' label='' onChange={e => this.setState({ description: e.target.value })} /></div>
                                    </div>


                                    <div className='col-12 p-1 center'><div className='col-9 actualPrice actualPriceMobile center'><p className='bold redT'> {((this.props.price / 100) * this.state.width * this.state.height).toLocaleString(undefined)} </p></div></div>





                                </div>
                                <div className='col-12 d-flex justify-content-end previousPrice addToCartMobileBtnWrapper '>
                                    <p className='marginalPTag_L'>70000</p>
                                    <span className=''>2%</span>
                                    <div className='col-12 p-2 mt-4 center'>
                                        <button type='submit' className='shop_Btn prodsHeading'>       </button>
                                    </div>
                                </div>

                            </Fragment>

                        </CSSTransition>

                    </div >

                </form >
            )

        }


        // Desktop
        if (!this.props.navBarSmallView) {




            return (
                <form type="submit" onSubmit={this.AddToCart} className='addToCartCont border bg-light'>

                    <div className='col-12 position-relative center flex-column'>

                        <CSSTransition
                            in={this.props.AddToCartStages.decisionToBuy}
                            timeout={100}
                            classNames="topToBottom"
                            unmountOnExit

                        >

                            <div className='col-12 center  decideToBuyBtnCont'>


                                <div className="col-10 d-flex flex-row justify-content-between">

                                    <Button variant="contained" className='col-6 DecideToShopBtn ' onClick={() => this.props.addToCartDicision({
                                        decisionToBuy: false,
                                        chooseSizes: true

                                    })}> </Button>
                                    <div className='col-6 center  productPriceContainer '> <p className='dirRtl redT'>  &nbsp; {this.props.price.toLocaleString(undefined)} 
                                    </p>  <i className='marginRightWeak'></i> </div>

                                </div>
                            </div>

                        </CSSTransition>


                        <CSSTransition
                            in={this.props.AddToCartStages.chooseSizes}
                            timeout={100}
                            classNames="topToBottom"
                            unmountOnExit

                        >
                            <Fragment>


                                <div className='col-12   pt-4'>

                                    <div className="center col-12">

                                        <div className='col-11  p-4  border-bottom center  '> <p className=' fontMedB redT'></p></div>
                                    </div>


                                    <div className="center col-12">
                                        <div className='col-10  p-4  border-bottom'><TextField id="outlined-basic" inputProps={{ color: "white" }} variant="outlined" type="number" required className='sizeInput col-12 mobileSizeInput chooseAddressBtnMobile ' label=' ( )' onChange={this.handleHeightChange} /></div>
                                    </div>

                                    <div className="center col-12">
                                        <div className='col-10 center p-4 border-bottom ' ><TextField id="outlined-basic" variant="outlined" type="number" required className='sizeInput col-12 mobileSizeInput  chooseAddressBtnMobile' label='  ( )' onChange={this.handleWidthChange} /></div>
                                    </div>

                                    <div className="center col-12">
                                        <Autocomplete required value={this.state.selecedVal1} onChange={(event, newValue) => {
                                            this.setState({ chainPosition: newValue })

                                        }} variant='contained' options={chainPositionOptions} className='chooseAddressBtn mobileSizeInput  chooseAddressBtnMobile p-4 border-bottom' renderInput={(params) => <TextField {...params} label="   " />}>  <ExpandMoreSharpIcon></ExpandMoreSharpIcon></Autocomplete>

                                    </div>

                                    <div className="center col-12">
                                        <Autocomplete required value={this.state.selecedVal1} onChange={(event, newValue) => {
                                            this.setState({ installationPosition: newValue })

                                        }} variant='contained' options={installationPosition} className='chooseAddressBtn mobileSizeInput  chooseAddressBtnMobile p-4 border-bottom' renderInput={(params) => <TextField {...params} label="   " />}>  <ExpandMoreSharpIcon></ExpandMoreSharpIcon></Autocomplete>

                                    </div>

                                    <div className="center col-12">
                                        <div className='col-10 center p-4 border-bottom'><TextField id="outlined-basic" variant="outlined" type="text" className='sizeInput col-12 mobileSizeInput chooseAddressBtnMobile' label='' onChange={e => this.setState({ description: e.target.value })} /></div>
                                    </div>


                                    <div className='col-12 p-1 center'><div className='col-9 actualPrice actualPriceMobile center'><p className='bold redT'> {((this.props.price / 10000) * this.state.width * this.state.height).toLocaleString(undefined)} </p></div></div>





                                </div>
                                <div className='col-12 d-flex justify-content-end previousPrice addToCartMobileBtnWrapper flex-column'>


                                    <div className='col-12 d-flex flex-row justify-content-between dirRtl p-3'>
                                        <span className='dicountContainder'>{this.props.data.discount}%</span>
                                        
                                        <p className='marginalPTag_L'>70000</p>
                                    </div>
                                    <div className='col-12 p-2 center'>
                                        <button type='submit' className='shop_Btn prodsHeading'>       </button>
                                    </div>
                                </div>

                            </Fragment>

                        </CSSTransition>

                    </div >

                </form>
            )
        }

    }
}

const mapStateToProps = (state) => {
    return {
        info: state.buyBasket.prodData,
        buyBasket: state.buyBasket,
        navBarSmallView: state.navBarSmallView,
        AddToCartStages: state.AddToCartStages


    }
}

const mapDispatchToProps = {
    setBuyBasket: setBuyBasket,
    addToCartDicision: addToCartDicision
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AddToCart))