import React, { Component } from 'react'
import { connect } from 'react-redux'
import Product from './Product'
import Card from './Card'
import { setProdData, setFirstPicData, setSecondPicData, setThirdPicData } from '../redux/actions'
import { Link } from 'react-router-dom'
import TelegramIcon from '@mui/icons-material/Telegram';
import InstagramIcon from '@mui/icons-material/Instagram';
import { Button } from '@mui/material'
import SmallImageRef from './SmallImageRef'
import CategoriesView from './CategoriesView'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

import { CSSTransition } from 'react-transition-group'
import { InView } from 'react-intersection-observer';

import { createRef } from 'react'
import { Fragment } from 'react'

export class productContainer extends Component {
    constructor() {
        super()

        this.initForImagesRef = new createRef()
    }


    state = {
        isCategorryOpen: false,
        firstImgIsInViewPort: false,
        secondImgIsInView: false,
        thirdImgIsInView: false,
        fourthImgIsInView: false,
    }


    getMainFirstData = () => {
        fetch("https://pardehmahoor.ir/digitalAssets/getMainMenu_first")
            .then(r => r.json())
            .then(d => this.props.setFirstPicData(d))

    }

    getMainSecData = () => {
        fetch("https://pardehmahoor.ir/digitalAssets/getMainMenu_Second")
            .then(r => r.json())
            .then(d => this.props.setSecondPicData(d.data))

    }

    getMainThirdData = () => {
        fetch("https://pardehmahoor.ir/digitalAssets/getMainMenu_Third")
            .then(r => r.json())
            .then(d => this.props.setThirdPicData(d.data))

    }


    handleIntersection = (entries) => {
        const firstImgIsInViewPort = entries[0].isIntersecting;
        this.setState({ firstImgIsInViewPort });


        this.setState({ firstImgIsInViewPort: true })

    }

    componentDidMount() {


        this.getMainFirstData()
        this.getMainSecData()
        this.getMainThirdData()
        fetch("https://pardehmahoor.ir/digitalAssets/prods")
            .then(res => res.json())
            .then(data => {
                {
                    this.props.setProdData(data.ProductData)
                    this.revProds = data.ProductData.reverse()

                }
            }

            )

    }



    componentWillUnmount() {


    }


    revProds = []
    render() {



        return (

            <div className=' center flex-row productContainer dirLtr '>
                <div className='col-12  productDeck d-flex flex-column flex-xl-row max_width border '>
                    <div className='col-12 col-xl-8 leftSideOfProds dirRtl oxHidd'>

                        <div className='col-12 center justify-content-start p-3 mainMenuTop'>
                            <h1 className='textGL d-flex '>
                                <span></span>,
                                <span></span>
                                <span></span>
                                <span> </span>
                                ...
                            </h1>

                        </div>
                        <div className="col-12 d-flex p-0 p-xl-3 m-0 center row minHeightProdCont">
                            {/* 
                            <InView as="div" onChange={(inView, entry) => {
                                this.setState({ firstImgIsInViewPort: inView })
                                console.log(inView)
                            }}>

                                <CSSTransition
                                    in={this.state.firstImgIsInViewPort}
                                    timeout={2000}
                                    classNames="opacLong"

                                >


                                    <div className={`d-flex position-relative center `} >

                                        <div className={`GuiderbuttonCont center  `}>
                                            <div className='center flex-column'>
                                                <p className='h2ForMainContent  fontSm' >  </p>
                                                <Button varient="contained" className='Guiderbutton'>  ,     !</Button>
                                                <p className='h2ForMainContent  fontSm' >    </p>
                                            </div>


                                        </div>

                                        <div className={`imageCont center position-relative ${this.props.navBarSmallView && "GuiderbuttonContMobile"}`}>
                                            {this.props.firstItemPic != null &&
                                                <Fragment>
                                                    <Link className="col-12 h-100" to={``}>
                                                        <img src={this.props.firstItemPic} className='imageForProdCont' alt="" />
                                                    </Link>
                                                </Fragment>
                                            }
                                        </div>



                                    </div>
                                </CSSTransition>
                            </InView> */}

                            {/* 
                            <InView className='col-12 center p-0 m-0' onChange={(inView, entry) => {
                                this.setState({ secondImgIsInView: inView })
                                console.log(inView)
                            }}>

                                <Fragment>

                                    <CSSTransition
                                        in={this.state.secondImgIsInView}
                                        timeout={1000}
                                        classNames="left-to-right"

                                    >


                                        <div className='col-12 flex-column flex-xl-row row p-0 m-0 center mt-1 d-flex justify-content-xl-between w_95_p  '>
                                            {this.props.secondImageData != null &&
                                                this.props.secondImageData.map(i =>
                                                    <Button className={`briefView m-0 p-0 ${this.props.navBarSmallView && "mt-2"} prodContImgMobileView`}>
                                                        <img className='col-12 h-100' src={i.imgUrl} />
                                                    </Button>
                                                )
                                            }

                                        </div>

                                    </CSSTransition>
                                </Fragment>

                            </InView> */}



                            <span ref={this.initForImagesRef}></span>
                            <InView className='col-12 row center justify-content-xl-between m-1 m-xl-0 p-0 w-838' onChange={(inView, entry) => {
                                this.setState({ fourthImgIsInView: inView })
                                console.log(inView)
                            }}>
                                <CSSTransition
                                    in={this.state.fourthImgIsInView}
                                    timeout={1000}

                                    classNames="right-to-left"

                                >

                                    <Fragment>
                                        {this.props.divDataWithImg != null &&
                                            this.props.divDataWithImg.slice(0, 6).map(i =>
                                                <CategoriesView imgUrl={i.imgUrl} name={i.name} ></CategoriesView>
                                            )
                                        }

                                        <CSSTransition
                                            in={this.state.isCategorryOpen}
                                            timeout={400}
                                            classNames="right-to-left"
                                            unmountOnExit
                                        >

                                            <div className='col-12 row center m-0 p-0 w-838 justify-content-xl-between'>
                                                {this.props.divDataWithImg != null &&
                                                    this.props.divDataWithImg.slice(6).map(i =>
                                                        <CSSTransition
                                                            in={this.state.isCategorryOpen}
                                                            timeout={1500}
                                                            classNames="right-to-left"
                                                            unmountOnExit
                                                        >

                                                            <CategoriesView imgUrl={i.imgUrl} name={i.name} ></CategoriesView>
                                                        </CSSTransition>
                                                    )}
                                            </div>
                                        </CSSTransition>
                                        {!this.state.isCategorryOpen &&

                                            <Button className='redT' onClick={() => this.setState({ isCategorryOpen: true })}><ExpandMoreIcon className="redT"></ExpandMoreIcon></Button>
                                        }
                                        {this.state.isCategorryOpen &&

                                            <Button className='redT' onClick={() => {
                                                this.setState({ isCategorryOpen: false })
                                                setTimeout(() => this.initForImagesRef.current.scrollIntoView({ behavior: 'smooth' }), 100)


                                            }}><ExpandLessIcon className='redT'></ExpandLessIcon></Button>
                                        }


                                    </Fragment>
                                </CSSTransition>
                            </InView>



                            <InView onChange={(inView, entry) => {
                                this.setState({ thirdImgIsInView: inView })
                                console.log(inView)
                            }}>

                                <CSSTransition
                                    in={this.state.thirdImgIsInView}
                                    timeout={1000}
                                    classNames="right-to-left"
                                    unmountOnExit
                                >

                                    <div className="col-12 center marginalDiv row p-0">
                                        {this.props.productData != null &&
                                            this.props.productData.slice(0, 5).map(i =>

                                                <SmallImageRef imgUrl={i.prodImgUrl[0]} title={i.title}></SmallImageRef>
                                            )
                                        }
                                    </div>
                                </CSSTransition>
                            </InView>



                            <div className='col-12 center marginalDiv'>
                                {this.props.thirdImageData != null &&
                                    this.props.thirdImageData.map(i =>
                                        <div className='FoucsUiGifCont w-838'>
                                            <img src={i.imgUrl} className='col-12 h-100 ' alt="" />
                                        </div>
                                    )
                                }
                            </div>



                        </div>

                    </div>

                    {!this.props.navBarSmallView &&
                        <div className='col-12 col-xl-4'>
                            <div className='col-12 d-flex justify-content-start p-5'>
                                <button className='prodsHeading' ><i className='fa fa-chevron-left' /><Link className="whiteT " to="/category//"> <ChevronLeftIcon />    </Link> </button>
                            </div>
                            {this.props.productData != null &&

                                this.revProds.slice(0, 3).map(i => (<Product title={i.title} price={i.price} img={i.imgUrl} data={i} />))}

                        </div>
                    }
                </div>
            </div >
        )
    }
}

const mapStateToProps = (state) => {
    return {
        productData: state.productData,
        divDataWithImg: state.divDataWithImg,
        navBarSmallView: state.navBarSmallView,
        firstItemPic: state.firstItemPic,
        firstItemDivision: state.firstItemDivision,
        firstItemSubDivision: state.firstItemSubDivision,
        firstItemProd: state.firstItemProd,
        secondImageData: state.secondImageData,
        thirdImageData: state.thirdImageData,
        navBarSmallView: state.navBarSmallView


    }
}

const mapDispatchToProps = {
    setProdData: setProdData,
    setFirstPicData: setFirstPicData,
    setSecondPicData: setSecondPicData,
    setThirdPicData: setThirdPicData
}

export default connect(mapStateToProps, mapDispatchToProps)(productContainer)
